  margin-top: 1em;
}
pre[name]:after {
  position: absolute;
  top: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.15);
  font-size: 13px;
  line-height: 1em;
  font-family: sans-serif;
  color: rgba(0, 0, 0, 0.75);
  padding: 0.25em 0.5em;
  content: attr(name);
  font-family: inherit;
}
