.contentModule {
  position: relative;
}
[showBorders3] .contentModule {
  position: relative;
  outline: 1px solid blue;
  background-color: rgba(0, 0, 255, 0.1);
}
[showBorders3] .contentModule::after {
  position: absolute;
  top: 0;
  right: 0;
  font-size: 11px;
  line-height: 13px;
  font-weight: 400;
  border: 1px solid blue;
  padding: 0.2em 0.3em;
  background-color: blue;
  color: white;
  pointer-events: none;
  content: attr(name);
  z-index: 3;
}
.circleIcon {
  position: relative;
  height: 30px;
  width: 30px;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  cursor: pointer;
  transition: all 0.2s ease;
}
.circleIcon::before {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  border-radius: inherit;
  border: 1px solid currentColor;
  content: "";
  opacity: 0.5;
  transition: inherit;
}
.circleIcon::after {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  border-radius: inherit;
  background-color: currentColor;
  content: "";
  opacity: 0.05;
  transition: inherit;
}
.circleIcon:active {
  transform: translateY(2px);
}
.circleIcon .BaseIcon {
  opacity: 0.75;
  transition: inherit;
}
.circleIcon:hover::before,
.circleIcon:hover .BaseIcon {
  opacity: 1;
}
.circleIcon:hover::after {
  opacity: 0.1;
}
.circleIcon--isDisabled {
  pointer-events: none;
}
.circleIcon--isDisabled::before {
  border-style: dashed;
  opacity: 0.35;
}
.circleIcon--isDisabled .BaseIcon {
  opacity: 0.35;
}
.richText a {
  transition: all 0.1s ease;
  opacity: 0.65;
}
.richText a:hover {
  opacity: 1;
  text-decoration: underline;
}
.richText p + p {
  margin-top: 0.65em;
}
.circleNumber {
  font-size: 11px;
  line-height: 1.3em;
  font-weight: 500;
  height: 15px;
  width: 15px;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  background-color: currentColor;
  border-radius: 50%;
  transform: translateY(-25%);
}
.circleNumber::before {
  content: attr(nr);
  line-height: 1em;
  color: white;
}
.pointer {
  cursor: pointer;
}
